<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>

import { wsEmit, wsOn, post } from 'api';

import { mapActions, mapMutations } from 'vuex';

export default {
    data() {
        return {
            audioCtx: new AudioContext()
        }
    },
    created() {
        const uid = window.localStorage.uid;
        if (uid) {
            // ÁõëÂê¨ÁªôÊàëÂèëÁßÅ‰ø°ÁöÑ‰∫ã‰ª∂...
            wsOn('receive-private-chat' ,(sender) => {
                this.audioTip()
                
                if (this.$route.name !== 'chat') {
                    const key = uid + '-' + sender.uid;
                    let num = this.getStroage(key) || 0;
                    if (!num || num == null) {
                        this.saveStroage({
                            [key]: ++num
                        });
                        location.reload(); 
                    } else {
                        this.$message({
                            message: `${sender.name}ÂèëÊù•‰∏ÄÊù°Êñ∞Ê∂àÊÅØ`,
                            type: 'success'
                        })
                    }
                    // this.saveStroage({
                    //     [key]: ++num
                    // });
                } else {
                    this.getShouldShowMsg({
                        uid1: uid,
                        uid2: sender.uid,
                    });
                    window.scrollTo({ 
                        top: window.screen.height + 9999, 
                        behavior: "smooth" 
                    });
                }
            });

        } 
    },
    computed: {
        
    },
    methods: {
        ...mapActions([
            'getShouldShowMsg'
        ]),
        audioTip() {
            // ÂàõÂª∫Èü≥È¢ë‰∏ä‰∏ãÊñá  
            var audioCtx = this.audioCtx;
            // ÂàõÂª∫Èü≥Ë∞ÉÊéßÂà∂ÂØπË±°  
            var oscillator = audioCtx.createOscillator();
            // ÂàõÂª∫Èü≥ÈáèÊéßÂà∂ÂØπË±°  
            var gainNode = audioCtx.createGain();
            // Èü≥Ë∞ÉÈü≥ÈáèÂÖ≥ËÅî  
            oscillator.connect(gainNode);
            // Èü≥ÈáèÂíåËÆæÂ§áÂÖ≥ËÅî  
            gainNode.connect(audioCtx.destination);
            // Èü≥Ë∞ÉÁ±ªÂûãÊåáÂÆö‰∏∫Ê≠£Âº¶Ê≥¢  
            oscillator.type = 'sine';
            // ËÆæÁΩÆÈü≥Ë∞ÉÈ¢ëÁéá  
            oscillator.frequency.value = 600.00;
            // ÂÖàÊääÂΩìÂâçÈü≥ÈáèËÆæ‰∏∫0  
            gainNode.gain.setValueAtTime(0, audioCtx.currentTime);
            // 0.01ÁßíÊó∂Èó¥ÂÜÖÈü≥Èáè‰ªéÂàöÂàöÁöÑ0ÂèòÊàê1ÔºåÁ∫øÊÄßÂèòÂåñ 
            gainNode.gain.linearRampToValueAtTime(2, audioCtx.currentTime + 0.01);
            // Â£∞Èü≥Ëµ∞Ëµ∑ 
            oscillator.start(audioCtx.currentTime);
            // 1ÁßíÊó∂Èó¥ÂÜÖÈü≥Èáè‰ªéÂàöÂàöÁöÑ1ÂèòÊàê0.001ÔºåÊåáÊï∞ÂèòÂåñ 
            gainNode.gain.exponentialRampToValueAtTime(0.001, audioCtx.currentTime + 1);
            // 1ÁßíÂêéÂÅúÊ≠¢Â£∞Èü≥ 
            oscillator.stop(audioCtx.currentTime + 1);
        }
    }
};
</script>


<style lang="less">
    body {
        margin: 0;
        padding: 0;
        overscroll-behavior-y: contain;
    }
</style>
